import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class LoginService {
  url_login = 'https://trix-server.herokuapp.com/auth';

  constructor(private http: HttpClient) { }

  login(usuario: number, senha: number, metadata: any): Observable<any> {
    return this.http.post<Boolean>(this.url_login, {
      usuario, senha, metadata,
    });
  }
}
