import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MensagensService {

  url_sendMensagem = 'https://trix-server.herokuapp.com/mensagem';
  url_social = 'https://api.caixa.gov.br:8443/sandbox/servicos-sociais/v1/consulta?NIS=13056725312';

  constructor(private http: HttpClient) { }

  sendMensagem(mensagem: string, contextos: any[]): Observable<Resposta> {
    return this.http.post<Resposta>(this.url_sendMensagem,
      { mensagem, contextos });
  }

  buscaApiSocial(retornoLogin: any): Observable<any> {
    const accessToken = retornoLogin.accessToken;
    return this.http.get<any>(this.url_social, {
      headers: {
        apikey: 'l7xx3e4479b454a04307acc6ee5e1258f9e6',
        Authorization: `bearer ${accessToken}`,
      }
    });
  }
}

export interface Mensagem {
  mensagem: string;
  contextos: [];
}

export interface Resposta {
  mensagem: string[];
  contextos: [];
  metadata: any;
}
