import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MensagensService } from './mensagens.service';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent {
  mensagens: Conversa[] = [];
  nomeCliente = 'Você';
  nomeBot = 'Trix';
  isLogin = false;
  showCortina = false;
  contextos = [];
  today = Date.now();
  beaconId = this.route.snapshot.paramMap.get("beaconId");
  metadata;
  accessToken = null;

  constructor(private mensagemService: MensagensService, private route: ActivatedRoute) { }

  public sendMensagem(campo) {
    const textoPergunta = campo.value;

    if (textoPergunta !== '') {
      this.addPergunta(textoPergunta);
      this.mensagemService.sendMensagem(textoPergunta, this.contextos).subscribe((res) => {
        this.contextos = res.contextos;
        this.metadata = res.metadata;
        if (res.mensagem[0] === 'LOGIN_NECESSARIO' && this.accessToken === null) {
          this.addResposta('Para essa solicitação, você será direcionado à tela de login. Aguarde...');
          this.showCortina = true;
          setTimeout(() => { this.isLogin = true; this.showCortina = false; }, 2000);
        } else if (res.mensagem[0] === 'LOGIN_NECESSARIO' && this.accessToken !== null) {
          const cAccess = {
            accessToken: this.accessToken,
          };
          this.resposdePergunta(cAccess);
        } else {
          res.mensagem.forEach((el) => {
            this.addResposta(el);
          });
        }
        const element = document.getElementById('chat-mensagens');
        this.scrollTo(element, element.scrollHeight, 500);
      });
    }

    this.limpaCampo(campo);
    campo.focus();
  }

  public saudacao() {
    const unidades = {
      '0647': 'na Agência Capital',
      '0001': 'na Agêncua Planalto',
      '9999': 'no Sindicato dos Bancários',
    }
    const mensagemDefault = 'Olá eu sou a Trix, a assistente virtual da Caixa. Caso você queira falar sobre o PIS ou benefícios sociais é só me falar.';
    const mensagemPersonalizada = `Olá eu sou a Trix, a assistente virtual da Caixa. Vi que você está ${unidades[this.beaconId]} e posso te ajudar com alguns serviços da Caixa por aqui. Caso você queira falar sobre o PIS ou benefícios sociais é só me falar.`;

    return (this.beaconId !== null) ? mensagemPersonalizada : mensagemDefault;
  }

  private addPergunta(textoPergunta) {
    const pergunta = {
      tipo: 'pergunta',
      interlocutor: this.nomeCliente,
      texto: textoPergunta,
      hora: Date.now(),
    };
    this.mensagens.push(pergunta);
  }

  private addResposta(textoResposta) {
    const resposta = {
      tipo: 'resposta',
      interlocutor: this.nomeBot,
      texto: textoResposta,
      hora: Date.now(),
    };
    this.mensagens.push(resposta);
  }

  private limpaCampo(campo) {
    campo.value = '';
  }

  private scrollTo(element, to, duration) {
    const start = element.scrollTop;
    const change = to - start;
    const increment = 20;
    let currentTime = 0;

    const easeInOutQuad = this.easeInOutQuad;
    const animateScroll = function() {
        currentTime += increment;
        const val = easeInOutQuad(currentTime, start, change, duration);
        element.scrollTop = val;
        if (currentTime < duration) {
            setTimeout(animateScroll, increment);
        }
    };
    animateScroll();
  }

  // t = current time
  // b = start value
  // c = change in value
  // d = duration

  private easeInOutQuad(t, b, c, d) {
    t /= d / 2;
    if (t < 1) {
      return c / 2 * t * t + b;
    }
    t--;
    return -c / 2 * (t * (t - 2) - 1) + b;
  }

  public closeLogin(retornoLogin: any) {
    this.accessToken = retornoLogin.accessToken;
    this.isLogin = false;
    if (!retornoLogin.login) {
      this.addResposta('Lamento, mas você não foi autenticado. Posso ajudar em algo mais?');
    } else {
      this.resposdePergunta(retornoLogin);
    }
  }

  resposdePergunta(retornoLogin) {
    this.contextos = [];
    this.mensagemService.buscaApiSocial(retornoLogin).subscribe((res) => {
      console.log(res);
      if (this.metadata.intentName === 'PIS - Consulta Valor') {
        if (res.dados_consulta_servicos_sociais.dados_pis.beneficios.beneficio[0]) {
          const valorPis = res.dados_consulta_servicos_sociais.dados_pis.beneficios.beneficio[0];
          this.addResposta(`A situação do seu PIS é "${valorPis.situacao}" e o valor para receber é ${valorPis.valor}`);
        } else {
          this.addResposta('Você não possui PIS para receber.');
        }
      }
    });
  }
}

export interface Conversa {
  tipo: string;
  interlocutor: string;
  texto: string;
  hora: number;
}
